const swiper = new Swiper(".swiper", {
	// Optional parameters
	direction: "horizontal",
	loop: true,
	slidesPerView: 1,
	spaceBetween: 60,
	// If we need pagination
	breakpoints: {
		// Ekrany o szerokości 640px lub mniej
		640: {
			slidesPerView: 1,
		},
		// Ekrany o szerokości 768px lub więcej
		768: {
			slidesPerView: 2,
		},
		// Ekrany o szerokości 1024px lub więcej
		1024: {
			slidesPerView: 3,
		},
	},
	loop: true,
    pagination: {
        el: '.swiper-pagination',
        clickable: true, // Możliwość klikania w kropki
    },
	// Navigation arrows
	navigation: {
		nextEl: ".swiper-button-next",
		prevEl: ".swiper-button-prev",
	},
})
